dding:3px;width:6rem;z-index:10}.carousel__btn-arrow--left{transform:rotate(135deg)}.carousel__btn-arrow--right{transform:rotate(-45deg)}.carousel__dots{display:inline-block;left:50%;margin-top:2rem;position:absolute;transform:translateX(-50%)}.carousel__dots .dot{background:#ccc;border:0;border-radius:50%;cursor:pointer;height:2rem;margin:0 .3rem;outline:none;transform:scale(0.5);width:2rem}.carousel__dots .dot.active{background:#000}